import { useState } from "react";

import socket from "./utils/socketConnection";

import Widget from "./components/Widget";
import Loading from "./components/Loading";

function App() {
  const [perfData, setPerfData] = useState({
    performanceData: {},
  });

  socket.on("data", (data) => {
    const currState = { ...perfData.performanceData };
    currState[data.macA] = data;
    setPerfData({
      performanceData: currState,
    });
  });

  return perfData && Object.keys(perfData.performanceData).length > 0 ? (
    <div className="App text-center p-20 px-5 md:pd-40">
      <h1 className="mb-4 text-4xl font-extrabold leading-none tracking-tight text-gray-900 md:text-5xl lg:text-6xl font-mono">
        CPU Data
      </h1>

      <div className="md:px-20">
        {Object.keys(perfData.performanceData).map((key) => (
          <Widget key={key} data={perfData.performanceData[key]} />
        ))}
      </div>
    </div>
  ) : (
    <Loading />
  );
}

export default App;
