import React from "react";

import Cpu from "./Cpu";
import Mem from "./Mem";
import Info from "./Info";

import "./widget.css";

const Widget = ({ data }) => {
  const {
    cpuLoad,
    freeMem,
    totalMem,
    usedMem,
    memUsage,
    osType,
    uptime,
    type,
    numOfCores,
    speed,
    macA,
    isActive,
  } = data;

  const cpu = { cpuLoad };
  const mem = { freeMem, usedMem, totalMem, memUsage };
  const info = { macA, osType, uptime, type, numOfCores, speed, isActive };

  return (
    <div className="w-full my-5 block p-6 bg-white border border-gray-200 rounded-lg shadow grid md:grid-cols-3">
      <Cpu cpuData={cpu} />
      <Mem memData={mem} />
      <Info data={info} />
    </div>
  );
};

export default Widget;
