import React from "react";

import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Doughnut } from "react-chartjs-2";

ChartJS.register(ArcElement, Tooltip, Legend);

const Mem = ({ memData }) => {
  const memUsagePercentage = Math.round(memData.memUsage * 100);

  let chartColor;
  if (memUsagePercentage < 20) {
    chartColor = "#00ff00";
  } else if (memUsagePercentage < 40) {
    chartColor = "#337ab7";
  } else if (memUsagePercentage < 60) {
    chartColor = "#f0ad4e";
  } else {
    chartColor = "#d9534f";
  }

  const data = {
    labels: ["Used Memory", "Free Memory"],
    datasets: [
      {
        label: "MEMORY",
        data: [memUsagePercentage, 100 - memUsagePercentage],
        backgroundColor: [chartColor, "rgb(229,231,235)"],
        borderColor: [chartColor, "rgb(229,231,235)"],
        borderWidth: 1,
      },
    ],
  };

  const totalMemInGB = Math.round((memData.totalMem / 1073741824) * 100) / 100;
  const freeMemInGB = Math.round((memData.freeMem / 1073741824) * 100) / 100;

  return (
    <div className="mem max-w-full my-10 md:my-0">
      <h3 className="text-xl text-bold text-center mb-4">Mem Usage</h3>

      <div className="canvas-wrapper w-full">
        <Doughnut
          data={data}
          options={{
            cutout: 150,
          }}
          className="w-full"
        />
        <div className="mem-text">{memUsagePercentage}</div>
      </div>

      <div>Total Memory: {totalMemInGB} GB</div>
      <div>Free Memory: {freeMemInGB} GB</div>
    </div>
  );
};

export default Mem;
