import React from "react";

import moment from "moment";

const Info = ({ data }) => {
  return (
    <div className="col-sm-3 col-sm-offset-1 cpu-info text-left max-w-full px-10">
      <h3 className="text-xl text-bold text-center mb-4">OS Info</h3>

      <h3 className="text-lg font-bold">Operating System</h3>
      <div className="widget-text">{data.osType}</div>
      <br />
      <h3 className="text-lg font-bold">Time Online</h3>
      <div className="widget-text">
        {moment.duration(data.uptime).humanize()}
      </div>
      <br />
      <h3 className="text-lg font-bold">Processor information</h3>
      <div className="widget-text">
        <strong>Type:</strong> {data.type}
      </div>
      <br />
      <div className="widget-text">
        <strong>Number of Cores:</strong> {data.numOfCores}
      </div>
      <br />
      <div className="widget-text">
        <strong>Clock Speed:</strong> {data.speed}
      </div>

      {data.isActive ? (
        <div className="bg-green-200 p-5 rounded mt-5 text-2xl font-bold text-green-900 text-center">
          Active
        </div>
      ) : (
        <div className="bg-red-200 p-5 rounded mt-5 text-2xl font-bold text-red-900 text-center">
          Offline
        </div>
      )}
    </div>
  );
};

export default Info;
