import React from "react";

import { Chart as ChartJS, ArcElement, Tooltip, Legend } from "chart.js";
import { Doughnut } from "react-chartjs-2";

ChartJS.register(ArcElement, Tooltip, Legend);

const Cpu = ({ cpuData }) => {
  let chartColor;
  if (cpuData.cpuLoad < 20) {
    chartColor = "#00ff00";
  } else if (cpuData.cpuLoad < 40) {
    chartColor = "#337ab7";
  } else if (cpuData.cpuLoad < 60) {
    chartColor = "#f0ad4e";
  } else {
    chartColor = "#d9534f";
  }

  const data = {
    labels: ["Cpu Load", "Free CPU Percentage"],
    datasets: [
      {
        label: "CPU LOAD",
        data: [cpuData.cpuLoad, 100 - cpuData.cpuLoad],
        backgroundColor: [chartColor, "rgb(229,231,235)"],
        borderColor: [chartColor, "rgb(229,231,235)"],
        borderWidth: 1,
      },
    ],
  };

  return (
    <div className="cpu max-w-full">
      <h3 className="text-xl text-bold text-center mb-4">CPU Load</h3>
      <div className="canvas-wrapper w-full">
        <Doughnut
          data={data}
          options={{
            cutout: 150,
          }}
          className="w-full"
        />
        <div className="cpu-text">{cpuData.cpuLoad}</div>
      </div>
    </div>
  );
};

export default Cpu;
